const colors = [
  '#FFFFFF',
  '#2196F3',
  '#4CAF50',
  '#FF9800',
  '#009688',
  '#795548',
];

console.log(colors.length - 1);

// Получение ссылок

const startButtonRef = document.querySelector('[data-action=start]');
const stopButtonRef = document.querySelector('[data-action="stop"]');

// Генерация случайного числа

const randomIntegerFromInterval = (min, max) => {
  return Math.floor(Math.random() * (max - min + 1) + min);
};

// Изменение  цвета
const changeBcgColor = i => {
  document.body.style.backgroundColor = colors[i];
};

const someF = () => {
  max = colors.length - 1;
  let i = randomIntegerFromInterval(0, max);
  changeBcgColor(i);
};

startButtonRef.addEventListener('click', () => {
  let timerId = setInterval(someF, 1000);
});

stopButtonRef.addEventListener('click', () => {
  clearInterval(timerId);
});
